import * as React from "react";
import { IOfferData } from "../config/appdata";
import { CustumGridChild } from "./CustumChildGridView";

/**
 * State for the Grid View React component
 */
export interface ICustumGridSate {
    OfferResult?: IOfferData[];
    PageSize?: number;
    PageNo?: number;
    OrderBy: string;
    SortBy: string;
    ShowChildIndex: number;
    ShowChildDetailIndex: number;
    DocPageNo: number;
    DocPageSize: number;
}

/**
 * Properties for the Grid View React component
 */
export interface ICustumGridProps {
    offerResult?: IOfferData[];
    pageSize?: number;
    pageNo?: number;
    orderBy: string;
    sortBy: string;
    onAddToSEETeamClick(e: any, selectedData: IOfferData, showView: boolean): any;
}

/**
 * Implementation of the Grid View component
 */
export class CustumGrid extends React.Component<ICustumGridProps, ICustumGridSate> {
    //constructor to initiiaise the state variable.
    private gridContainer: any = null;
    constructor(props: ICustumGridProps) {
        super(props);
        this.gridContainer = React.createRef();
        this.state = {
            OrderBy: this.props.orderBy,
            SortBy: this.props.sortBy,
            OfferResult: this.props.offerResult,
            PageNo: this.props.pageNo,
            PageSize: this.props.pageSize,
            ShowChildIndex: -1,
            ShowChildDetailIndex: -1,
            DocPageNo: 1,
            DocPageSize: 5
        };
    }

    //to register the mouse click event 
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }

    //to un-register the mouse click event 
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }


    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        const showChildIndex = this.state.PageNo == nextProps.PageNo ? this.state.ShowChildIndex : -1;
        this.setState({
            OfferResult: nextProps.offerResult
            , PageSize: nextProps.pageSize
            , PageNo: nextProps.pageNo
            , OrderBy: nextProps.orderBy
            , SortBy: nextProps.sortBy
            , ShowChildIndex: showChildIndex
        });
    }


    //to render the Grid view UI
    public render() {
        const offerResultUI = this.getOfferUI();
        return (<div ref={this.gridContainer} className="offersListContainer offersListWrap" role="table" style={{ display: "block" }}>
            <div className="new-table-row header"  role="header">
                <div title="Files" role="columnheader" className="new-column new-act-th commonClassForFilesHeaderColumn offerStringHeader"
                    data-columnname="DocCount" id="SS-Content-Offer-Files-Header">Files<span className="sorting">
                        <i className="ms-Icon ms-Icon--SortDown x-hidden-focus titleSortDown ListSortDown"
                            style={{ display: "none" }} title="Descending" aria-hidden="true"></i>
                        <i className="ms-Icon ms-Icon--SortUp x-hidden-focus titleSortUp ListSortUp"
                            style={{ display: "none" }} title="Ascending" aria-hidden="true"></i></span>
                </div>
                <div title="Title" role="columnheader" className="newWrapper new-attributes offerStringHeader" id="SS-Content-Offer-Title-Header"
                    data-columnname="Title">
                    <div className="newWrapper new-title-comment-module-reporter">
                        <div className="newWrapper new-title-comment">
                            <div className="new-column title new-act-th">Title<span className="sorting">
                                <i onClick={(e) => this.onHeaderSortClick(e, "titles")} className="ms-Icon ms-Icon--SortDown x-hidden-focus titleSortDown ListSortDown"
                                    style={{ display: ((this.state.SortBy === "titles" && this.state.OrderBy === "d") ? "block" : "none") }}
                                    title="Descending"
                                    aria-hidden="true"></i>
                                <i onClick={(e) => this.onHeaderSortClick(e, "titles")} className="ms-Icon ms-Icon--SortUp x-hidden-focus titleSortUp ListSortUp"
                                    style={{ display: ((this.state.SortBy === "titles" && this.state.OrderBy === "a") ? "block" : "none") }}
                                    title="Ascending" aria-hidden="true"></i></span></div>
                        </div>
                    </div>
                </div>
                <div role="columnheader" className="newWrapper offerStringHeader" title="Modified" id="SS-Content-Offer-Modified-Header" data-columnname="Modified">
                    <div className="new-column modified new-act-th">Modified<span className="sorting">
                        <i className="ms-Icon ms-Icon--SortDown x-hidden-focus titleSortDown ListSortDown"
                            style={{ display: "none" }} title="Descending" aria-hidden="true"></i>
                        <i className="ms-Icon ms-Icon--SortUp x-hidden-focus titleSortUp ListSortUp"
                            style={{ display: "none" }} title="Ascending" aria-hidden="true"></i></span></div>
                </div>
                <div role="columnheader" className="newWrapper">
                    <div title="Related IPKits" id="SS-Content-Offer-RelatedIPKits-Header"
                        data-columnname="Related IPKits"
                        className="new-column relatedIpkits new-act-th">Related IPKits
<span className="sorting">
                            <i className="ms-Icon ms-Icon--SortDown x-hidden-focus titleSortDown ListSortDown" style={{ display: "none" }}
                                title="Descending" aria-hidden="true"></i>
                            <i className="ms-Icon ms-Icon--SortUp x-hidden-focus titleSortUp ListSortUp"
                                style={{ display: "none" }} title="Ascending" aria-hidden="true"></i></span>
                    </div>
                </div>
                <div className="newWrapper">
                    <div title="" className="new-column more new-act-td"></div>
                </div>
            </div>
            {offerResultUI}
        </div>
        );
    }

    //to handle the click out side event 
    public handleClickOutside = (event: any) => {
        try {
            if (this.gridContainer.current && !this.gridContainer.current.contains(event.target)) {
                if (this.state.ShowChildDetailIndex != -1)
                    this.setState({ ShowChildDetailIndex: -1 });
            }
        } catch (error) {
            // Logging.AppInsightsTrackException(AppSetting["appName"], "Exception occured at handleClickOutside() in " + this.fileName, error);
        }
    }

    //to get the offer ui
    private getOfferUI() {
        let currOfferResult = this.state.OfferResult === undefined ? [] : this.state.OfferResult;
        const pageSize = this.state.PageSize === undefined ? 10 : this.state.PageSize;
        const pageNo = this.state.PageNo === undefined ? 1 : this.state.PageNo;
        let offerResultUI: any = null;
        if (currOfferResult && currOfferResult.length > 0) {
            if (this.state.SortBy === "titles") {
                currOfferResult.sort((a, b) => {
                    let returnValue = 0;
                    const val1 = String(a.Title).toLowerCase().trim();
                    const val2 = String(b.Title).toLowerCase().trim();
                    returnValue = val1.localeCompare(val2);
                    return this.state.OrderBy === "a" ? returnValue : -1 * returnValue;
                });
            }

            currOfferResult.forEach((obj, index) => { obj.SrNo = (index + 1); });

            currOfferResult = currOfferResult.filter(obj => obj.SrNo >= ((pageSize * (pageNo - 1) + 1))
                && obj.SrNo <= (pageSize * pageNo));

            offerResultUI = currOfferResult.map((item, index) => {
                const className = this.state.ShowChildIndex === index ? "main-row offerlistview expandList" : "main-row offerlistview";
                return <div className={className} data-value="1561" style={{ height: "auto", display: "block" }}>
                    <div className="new-table-row listparentItem">
                        <div className="new-column newFiles new-files-child SS-Content-Offers-List-1561"
                            id="SS-Content-Offers-List-0" title="Collapse Offers" aria-label="Collapse Offers"
                            onClick={(e) => this.showChildView(e, index, item.FileCount)}>
                            <span className="newFilescountDocuments" role="button" aria-label="18. Azure Workloads for SAP. collapsed"><span aria-hidden="true">{item.FileCount}</span></span>
                            <i className="ms-Icon x-hidden-focus ms-Icon--CaretSolid" aria-hidden="true"></i>
                        </div>
                        <div className="newWrapper new-attributes">
                            <div className="newWrapper new-title-comment-module-reporter">
                                <div className="newWrapper new-title-comment"><div className="new-column newTitle new-act-td">{item.Title}</div></div>
                            </div>
                        </div>
                        <div className="newWrapper">
                            <div title="Oct" className="new-column modified new-act-td">
                            </div>
                        </div>
                        <div className="newWrapper"><div className="new-column relatedIpkits new-act-td">
                            <a className="outline disabled visibleflse"
                                title="" target="_blank" rel="noreferrer">Related IPKits</a></div>
                        </div>
                        <div className="newWrapper" role="cell">
                            <div className="new-column more right-ellipsis new-act-td child-Ellipses-more"
                                title="Open Menu" role="button"
                                aria-expanded="true" aria-label="Azure Workloads for SAPOpen Menu">
                                <span><i onClick={(e) => this.ShowChildDetailView(e, index)}
                                    className="ms-Icon ms-Icon--More x-hidden-focus"></i></span>
                            </div>
                            <div className="showEllipseMenu" style={{ display: (this.state.ShowChildDetailIndex === index ? "block" : "none") }}
                                title="Azure Workloads for SAP" id="SS-Offer-List-AddtoSEETeam-0">
                                <ul className="EllipseMenuItemUL">
                                    <li onClick={(e) => this.onShowAddToSEETeamClick(e, index, item)} className="AddToKMapMenuItem EllipseMoreItem"
                                        title="Add to SEE Team(s)"><span className="EllipseMenuItemImg">
                                            <i className="ms-Icon ms-Icon--Nav2DMapView"
                                                title="Add to SEE Team(s)" aria-hidden="true"></i></span>
                                        <div className="EllipseMenuItem"><p><span>Add to SEE Team(s)</span>
                                        </p></div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div style={{ display: (this.state.ShowChildIndex === index ? "block" : "none") }}
                        className="childwrapper childDocumentHeaderSSUXItemInfo SS-Content-Offers-Documents-List-Header-0" id="SS-Content-Offers-Documents-List-Header-1561" role="table">
                        <div className="containerBeaks"><span className="ContBeaksForOffers"></span></div>
                        <div className="new-table-row childHeader documentsList" role="row">
                            <div className="newWrapper new-attributes">
                                <div className="newWrapper new-title-comment-module-reporter" role="columnheader">
                                    <div className="newWrapper docStringHeader docStringChild" id="SS-Offers-Sorting-Child-File-1561" data-columnname="fileType"><span className="docHeaderPageIcon"><i title="File Type" role="columnheader" aria-label="File type sortable link. press enter to sort" className="ms-Icon ms-Icon--Page"></i><span className="sorting"><i className="ms-Icon ms-Icon--SortDown x-hidden-focus titleSortDown ListSortDown" style={{ display: "none" }} title="Descending" aria-hidden="true"></i><i className="ms-Icon ms-Icon--SortUp x-hidden-focus titleSortUp ListSortUp" style={{ display: "none" }} title="Ascending" aria-hidden="true"></i></span></span></div>
                                    <div data-columnname="docTitle" className="newWrapper new-title-comment doctitle docStringChild" id="SS-Offers-Sorting-Child-Title-1561" role="columnheader" aria-label="press enter to sort">
                                        <div title="Title" className="new-column newTitle new-act-th"> TITLE <span className="sorting">
                                            <i className="ms-Icon ms-Icon--SortDown x-hidden-focus titleSortDown ListSortDown" style={{ display: "none" }} title="Descending" aria-hidden="true"></i><i className="ms-Icon ms-Icon--SortUp x-hidden-focus titleSortUp ListSortUp" style={{ display: "none" }} title="Ascending" aria-hidden="true"></i></span></div>
                                    </div>
                                </div>
                            </div>
                            <div data-columnname="ModifedDate" className="newWrapper docmodified docStringChild" id="SS-Offers-Sorting-Child-Modified-1561" role="columnheader" aria-label="press enter to sort">
                                <div title="Modified" className="new-column modified new-act-th"> MODIFIED <span className="sorting"><i className="ms-Icon ms-Icon--SortDown x-hidden-focus titleSortDown ListSortDown" style={{ display: "none" }} title="Descending" aria-hidden="true"></i><i className="ms-Icon ms-Icon--SortUp x-hidden-focus titleSortUp ListSortUp" style={{ display: "none" }} title="Ascending" aria-hidden="true"></i></span></div>
                            </div>
                            <div className="newWrapper ssuxPagination--pagination-nav" role="columnheader">
                                <div className="new-column pagination Pagination-show-hideOffer-Docs-1561 new-act-th">
                                    <div className="paginationParent">
                                        <div className="paginationcount">
                                            <i onClick={(e) => this.onPreviousePageClick(e)}
                                                className={this.state.DocPageNo == 1 ?
                                                    "ms-Icon ms-Icon--ChevronLeftMed ssuxPagination--paginationOffer-Docs-1561 prev disable"
                                                    : "ms-Icon ms-Icon--ChevronLeftMed ssuxPagination--paginationOffer-Docs-1561 prev"} title="Previous page"
                                                id="SS-PaginationOffer-DocsPrev-1561"></i>
                                            <span className="ss-pagination-counterOffer-Docs-1561">{this.state.DocPageNo}</span>
                                            <i onClick={(e) => this.onNextPageClick(e, (item.Docs ? item.Docs.length : 0))}
                                                className={(item.Docs && item.Docs.length > 0 && this.state.DocPageNo < Math.ceil((item.Docs.length / this.state.DocPageSize))) ?
                                                    "ms-Icon ms-Icon--ChevronRightMed ssuxPagination--paginationOffer-Docs-1561 next"
                                                    : "ms-Icon ms-Icon--ChevronRightMed ssuxPagination--paginationOffer-Docs-1561 next disable"
                                                }
                                                title="Next page" id="SS-PaginationOffer-DocsNext-1561">
                                            </i></div>
                                        <div className="dropdown" tabIndex={0} aria-expanded="false" aria-label="Rows per page 4 Rows menu" role="button" aria-haspopup="listbox">
                                            <div className="dropdown-toggle dropdowndiv-Offer-Docs-1561" id="dropdown-toggle-Offer-Docs-1561" data-toggle="dropdown">5 rows</div>
                                            <ul onKeyDown={(e) => { alert("hi"); }} className="dropdown-menu dropdownul-Offer-Docs-1561 dropdownMenuClose" id="dropdown-menuOffer-Docs-1561" role="listbox" aria-labelledby="menu1">
                                                <li><span className="paginationDropdownBeak"></span></li>
                                                <h4 role="presentation" className="dropdown-header">Rows per page</h4>
                                                <li role="option" aria-selected="true" tabIndex={0} aria-label="Current Menu item 5" >
                                                    <p><span className="paginationCheckMark"><i title="" className="ms-Icon ms-Icon--CheckMark"></i></span>5</p>
                                                </li>
                                                <li role="option" aria-selected="false" tabIndex={0} aria-label="Current Menu item 10" >
                                                    <p><span className="paginationCheckMark"><i title="" className="ms-Icon ms-Icon--CheckMark hidePaginationIcon"></i></span>10</p>
                                                </li>
                                                <li role="option" aria-selected="false" tabIndex={0} aria-label="Current Menu item 20" >
                                                    <p><span className="paginationCheckMark"><i title="" className="ms-Icon ms-Icon--CheckMark hidePaginationIcon"></i></span>20</p>
                                                </li>
                                                <li role="option" aria-selected="false" tabIndex={0} aria-label="Current Menu item 50" >
                                                    <p><span className="paginationCheckMark"><i title="" className="ms-Icon ms-Icon--CheckMark hidePaginationIcon"></i></span>50</p>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {
                            item.Docs && item.Docs.length > 0 ?
                                item.Docs.map((o) => {
                                    return o.OID > 0 && o.SrNo >= ((this.state.DocPageSize * (this.state.DocPageNo - 1) + 1))
                                        && o.SrNo <= (this.state.DocPageSize * this.state.DocPageNo) ? <CustumGridChild rowData={o} /> : null;
                                })
                                : null
                        }
                    </div>
                </div>;
            });

        }
        return offerResultUI;
    }

    //to show the child grid view 
    public showChildView(e: any, index: number, fileCount?: number) {
        e.stopPropagation();
        e.preventDefault();
        if (fileCount && fileCount > 0)
            this.setState({ ShowChildIndex: (index === this.state.ShowChildIndex ? -1 : index), DocPageNo: 1 });
    }

    //to show the child grid view paging on next 
    public onNextPageClick(e: any, totalRec: number) {
        e.stopPropagation();
        let pageNo = this.state.DocPageNo;
        if (totalRec && pageNo < Math.ceil((totalRec / this.state.DocPageSize))) {
            pageNo = pageNo + 1;
            this.setState({ DocPageNo: pageNo });
        }
    }


    //to show the child grid view paging on next 
    public onPreviousePageClick(e: any) {
        e.stopPropagation();
        let pageNo = this.state.DocPageNo;
        if (pageNo > 1) {
            pageNo = pageNo - 1;
            this.setState({ DocPageNo: pageNo });
        }
    }
    //to show the the child detail view 
    public ShowChildDetailView(e: any, index: number) {
        e.stopPropagation();
        e.preventDefault();
        this.setState({ ShowChildDetailIndex: (index === this.state.ShowChildDetailIndex ? -1 : index) });
    }

    //to show the add to see team popup
    public onShowAddToSEETeamClick(e: any, index: number, item: IOfferData) {
        e.stopPropagation();
        e.preventDefault();
        this.setState({ ShowChildDetailIndex: -1 });
        this.props.onAddToSEETeamClick(e, item, true);
    }

    //to sort the grid on header click
    public onHeaderSortClick(e: any, sortBy: string) {
        e.stopPropagation();
        e.preventDefault();
        this.setState({ OrderBy: (this.state.OrderBy === "a" ? "d" : "a"), SortBy: sortBy });
    }
}

