import { Settings } from "../config/commonconfig";
import { ISSUXData, IOfferData, IFilterData, IDocument, JoinedTeams, AddToSEETeamsRef, AddToSEETeams } from "../config/appdata";



//class to call the API's 
export class GraphService {
    constructor() {
    }

    //to get the appKeyID

    public getAppKeyID = async (formKeyID?: string, token?: string): Promise<string> => {
        let formKey: string = formKeyID ? formKeyID : "";
        const myReturnList: ISSUXData = { value: [], MSProduct: [], count: 0, ServicesDomain: [] };
        if (formKey.length <= 0) {
            const formKeyHeader = new Headers({ Accept: "application/json;", Authorization: `Bearer ${token}` });
            const formKeyOptions = {
                headers: formKeyHeader
            };
            const keyResponse = await fetch(Settings.AppSettings.fromKeyURL, formKeyOptions);
            const keyResponseBody = keyResponse.json();
            if (keyResponse)
                formKey = await keyResponseBody as string;

        }
        console.log("formKey : ", formKey);
        return formKey;
    }
    //to get the package data from the api
    public getPackageData = async (searchText: string, filter: IFilterData, formKeyID?: string, token?: string): Promise<ISSUXData> => {

        let formKey: string = formKeyID ? formKeyID : "";

        const myReturnList: ISSUXData = { value: [], MSProduct: [], count: 0, ServicesDomain: [] };
        if (formKey.length <= 0) {
            formKey = await this.getAppKeyID(formKey, token);

        }
        console.log("formKey : ", formKey);
        const headers = new Headers({
            "Accept": "application/json; odata=verbose", "api-key": formKey, "x-ms-azs-return-searchid": "true"
            , "Access-Control-Expose-Headers": "x-ms-azs-searchid", "access-control-allow-origin": "*"
        });
        const options = {
            headers
        };
        let offerURL = Settings.AppSettings.offerURL;//.replace("`searchTextReplace`", searchText);
        offerURL += "?api-version=2016-09-01";
        if (searchText && searchText.length > 0)
            offerURL += "&search=%2F.*" + searchText;
        offerURL += ".*%2F&$top=500";

        offerURL += "&$filter=search.in(ReleaseStatus,'New,Active,Retiring Soon,Updated',',')";
        if (filter && filter.MSProduct && filter.MSProduct.length > 0)
            offerURL += " and MSProducts/any(t:search.in(t, '" + filter.MSProduct.join(",") + "',','))";

        if (filter && filter.ServiceDomain && filter.ServiceDomain.length > 0)
            offerURL += " and ServicesDomain/any(t: search.in(t, '" + filter.ServiceDomain.join(",") + "', ','))";

        offerURL += "&facet=Title,count:50,sort:value&facet=MSProducts,count:50,sort:value&facet=VerticalIndustries,count:50,sort:value";
        offerURL += "&facet=ServicesDomain,count:50,sort:value&$count=true";
        console.log("Option : ", options);

        const response = await fetch(offerURL, options);
        const body: ISSUXData = await response.json() as ISSUXData;
        if (body && body.value && body.value.length > 0) {
            const valueList: IOfferData[] = [];
            body.value.forEach((item, index) => {
                valueList.push({ SrNo: (index + 1), OID: item.OID, Docs: [], Title: item.Title, FileCount: item.FileCount, Description: item.Description });
            });
            myReturnList.value = valueList;
            myReturnList.fomrKeyID = formKey;
            myReturnList.count = body["@odata.count"];
            myReturnList.MSProduct = body["@search.facets"].MSProducts;
            myReturnList.ServicesDomain = body["@search.facets"].ServicesDomain;

            let oids: number[] = [];
            if (myReturnList && myReturnList.value && myReturnList.value.length > 0) {
                for (let i = 0; i < 10 && i < myReturnList.value.length; i++) {
                    myReturnList.value[i].Docs.push({ OID: -1, DocURL: "", Title: "", SrNo: -1 });
                    oids.push(myReturnList.value[i].OID);
                }
            }
            const offerDoc: IDocument[] = await this.getOfferDocument(oids, formKey);

            if (offerDoc && offerDoc.length > 0) {
                myReturnList.value.forEach(obj => {
                    let currDocument = offerDoc.filter(o => o.OID == obj.OID);
                    if (currDocument && currDocument.length > 0) {
                        currDocument.forEach((c, index) => obj.Docs.push({ ...c, SrNo: (index + 1) }));
                    }
                });
            }
        }
        return myReturnList;
    }


    //to get the package data from the api
    public getOfferDocument = async (oid: number[], formKeyID?: string): Promise<any> => {
        const myReturnList: IDocument[] = [];
        if (oid && oid.length > 0) {
            const formKey = formKeyID ? formKeyID : "";
            const headers = new Headers({ "Accept": "application/json;", "api-key": formKey, "x-ms-azs-return-searchid": "true", "Access-Control-Expose-Headers": "x-ms-azs-searchid" });
            const options = {
                headers
                , crossDomain: true
            };
            let azureUrl = Settings.AppSettings.idrURL;
            azureUrl += "/campusdocs/docs?$orderby=ViewsRecent desc &$filter=";
            oid.forEach((obj, index) => {
                azureUrl += (index == 0 ? "OID/any(t: t eq '" + obj + "')" : "or OID/any(t: t eq '" + obj + "')")
            })
            azureUrl = azureUrl + "&$select=OID,Title,AssetUrl &api-version=2016-09-01-PREVIEW";
            const response = await fetch(azureUrl, options);
            const body = await response.json();
            if (body && body.value && body.value.length > 0) {
                body.value.forEach((item, index) => {
                    if (item && item.OID && item.OID.length > 0) {
                        item.OID.forEach((obj, index) => {
                            if (oid.find(o => o == obj))
                                myReturnList.push({ OID: obj, Title: item.Title, DocURL: item.AssetUrl, SrNo: (index + 1) });
                        })
                    }
                });
            }
            return myReturnList;
        }
        else {
            return myReturnList;
        }
    }


    //to get the joined team data
    public getJoinedTeams = async (token: string): Promise<JoinedTeams[]> => {
        const myReturnList: JoinedTeams[] = [];
        const headers1 = new Headers({ Accept: "application/json;", Authorization: `Bearer ${token}` });
        const options = {
            headers: headers1
        };
        let azureUrl = Settings.AppSettings.teamsURL;
        const response = await fetch(azureUrl, options);
        const body = await response.json();
        if (body && body.value && body.value.length > 0) {
            body.value.forEach((item, index) => {
                if (item && item.id && item.id.length > 0) {
                    myReturnList.push({ ID: item.id, Name: item.displayName });
                }
            });
        }
        return myReturnList;

    }

    //to get the joined team data
    public getAddToSEETeamsInfo = async (oid: number, title: string, token: string): Promise<AddToSEETeamsRef> => {
        const myReturnList: AddToSEETeamsRef = { OID: oid, Title: title };
        const headers1 = new Headers({ Accept: "application/json;", Authorization: `Bearer ${token}` });
        const options = {
            headers: headers1
        };
        let azureUrl = Settings.AppSettings.addToSEETeamURL;
        if (oid && oid > 0)
            azureUrl = azureUrl + "?OID=" + oid + "&IPKitID=undefined&DocumentURL=undefined&ProjectsiteId";
        else
            azureUrl = azureUrl + "?OID=-1&IPKitID=undefined&DocumentURL=undefined&ProjectsiteId";
        const response = await fetch(azureUrl, options);
        const body = await response.json();
        const data: AddToSEETeams[] = [];
        if (body && body.length > 0) {
            body.forEach((item, index) => {
                if (item && item["$id"] && item["$id"].length > 0) {

                    let addToSEETeamObj: AddToSEETeams = {
                        ID: item["$id"], ProjectSiteID: item.ProjectSiteID
                        , Title: item.Title, ProjectSiteUrl: item.ProjectSiteUrl
                        , GroupId: item.GroupId, Name: item.Title
                        , IsExist: item.IsExist, SeeTeamsNotification: item.SeeTeamsNotification
                    }
                    if (item.channelsName && item.channelsName.length > 0) {
                        addToSEETeamObj.Channel = [];
                        item.channelsName.forEach((o, i) => {
                            if (o && o.toLowerCase() != "general") {
                                addToSEETeamObj.Channel && addToSEETeamObj.Channel.push({
                                    ID: (i + 1)
                                    , Title: o
                                    , ChannelName: o
                                    , IsAdded: false
                                })
                            }
                        });
                    }
                    data.push(addToSEETeamObj);
                    myReturnList.AddToSEETeamsData = data;
                }
            });
        }
        return myReturnList;

    }

    //to get the joined team data
    public saveAddToSEETeamsInfo = async (token: string, oid: number, title?: string, projectSiteInfo?: string[]): Promise<string> => {
        let myReturnList: string = "";
        const headers1 = new Headers({ Accept: "application/json;", Authorization: `Bearer ${token}`, "Content-Type": "application/json" });
        const options = {
            headers: headers1
            , method: 'POST'
            , "Content-Type": "application/json"
            , crossDomain: true
            , body: JSON.stringify({ "oID": oid, "Title": title, "createdBySource": 2 })
        };
        let azureUrl = Settings.AppSettings.saveAddToSEETeamsURL;
        if (projectSiteInfo && projectSiteInfo.length > 0)
            azureUrl = azureUrl + "?ProjectSiteIDAndSeeTeams=" + projectSiteInfo;
        {
            const response = await fetch(azureUrl, options);
            const body = await response.json();
            myReturnList = body;
        }
        return myReturnList;

    }
    public getData = (token) => {
        const headers1 = new Headers({ Accept: "application/json;", Authorization: `Bearer ${token}` });
        const options1 = {
            headers: headers1
        };

        Promise.all([
            fetch("https://graph.microsoft.com/v1.0/me/joinedTeams", options1),
            fetch("https://campuswebapi-stg.azurewebsites.net/api/knowledgemap/SeeTeamRecentProject?OID=3654&IPKitID=undefined&DocumentURL=undefined&ProjectsiteId", options1)
        ]).then((responses) => {
            // Get a JSON object from each of the responses
            return Promise.all(responses.map((response) => {
                return response.json();
            }));
        }).then((data) => {
            // Log the data to the console
            // You would do something with both sets of data here
            if (data && data.length > 1) {
                console.log(data);
            }
        }).catch((error) => {
            // if there's an error, log it
            console.log(error);
        });
    }
}








