    //to handle the toggle vertical filer even 
    public toggleVerticalFilter(e: any, status: boolean) {
        try {
            e.stopPropagation();
            e.preventDefault();
            if (this.state.searchText && this.state.searchText.length > 0
                && (this.state.productList && this.state.productList.data && this.state.productList.data.length > 0 ||
                    this.state.domainList && this.state.domainList.data && this.state.domainList.data.length > 0))
                this.setState({ showVerticalFilter: status });
            else
                this.setState({ showVerticalFilter: false });
        } catch (error) {
            //  Logging.AppInsightsTrackException(AppSetting["appName"], "Exception occured at toggleVerticalFilter() in " + this.fileName, error);
        }
    }

    //to handle the paging change event 
    public async onCustumPagingChange(e: any, pageSize: number, pageNo: number) {
        try {

            let oids: number[] = [];
            let currOfferResult = [...this.state.offerResult || []];
            currOfferResult = currOfferResult.filter(obj => obj.SrNo >= ((pageSize * (pageNo - 1) + 1))
                && obj.SrNo <= (pageSize * pageNo));

            if (currOfferResult && currOfferResult.length > 0) {
                for (let i = 0; i < 10 && i < currOfferResult.length; i++) {
                    if (currOfferResult[i].Docs === null || currOfferResult[i].Docs == undefined || currOfferResult[i].Docs.length == 0) {

                        currOfferResult[i].Docs.push({ OID: -1, DocURL: "", Title: "", SrNo: -1 });
                        oids.push(currOfferResult[i].OID);
                    }
                }
            }
            if (oids && oids.length > 0) {
                //this.setState({ isLoading: true });
                this.graphService = new GraphService();
                const offerDoc: IDocument[] = await this.graphService.getOfferDocument(oids, this.state.formKeyID);
                if (offerDoc && offerDoc.length > 0) {
                    currOfferResult.forEach(obj => {
                        let currDocument = offerDoc.filter(o => o.OID == obj.OID);
                        if (currDocument && currDocument.length > 0) {
                            currDocument.forEach((c, index) => obj.Docs.push({ ...c, SrNo: (index + 1) }));
                        }
                    });
                }
            }
            this.setState({ pageSize, pageNo });
        } catch (error) {
            // Logging.AppInsightsTrackException(AppSetting["appName"], "Exception occured at onCustumPagingChange() in " + this.fileName, error);
        }
    }

