    //to handle the Add to SEE team click 
    public async onAddToSEETeamClick(e: any, selectedData: IOfferData, showView: boolean) {
        try {
            if (this.state.showAddToSEETeams != showView) {
                if (showView) {
                    if (this.state.addToSEETeamsRef && this.state.addToSEETeamsRef.OID == selectedData.OID) {
                        this.setState({ showAddToSEETeams: showView });
                    }
                    else {
                        this.authService = new AuthService();
                        if (this.state.joinedTeams && this.state.joinedTeams.length > 0) {
                            this.setState({ isTeamLoading: true, showAddToSEETeams: showView, isSavingSEETeams: false }, () => {
                                this.authService.getTokenWithLoginPopup("campusapi").then(
                                    token => {
                                        this.graphService.getAddToSEETeamsInfo(selectedData.OID, selectedData.Title, token).then(obj => {
                                            this.setState({ addToSEETeamsRef: obj, isTeamLoading: false, showAddToSEETeams: showView, isTeamCreated: false });
                                        });
                                    });
                            });
                        }
                        else {
                            this.setState({ isTeamLoading: true, showAddToSEETeams: showView, isSavingSEETeams: false }, () => {
                                this.authService.getTokenWithLoginPopup("team").then(
                                    token => {
                                        this.graphService.getJoinedTeams(token).then(joinedTeamObj => {
                                            if (joinedTeamObj && joinedTeamObj.length > 0) {
                                                this.authService.getTokenWithLoginPopup("campusapi").then(
                                                    token => {
                                                        this.graphService.getAddToSEETeamsInfo(selectedData.OID, selectedData.Title, token).then(seeTeamsObj => {
                                                            this.setState({ isTeamLoading: false, joinedTeams: joinedTeamObj, isTeamCreated: false, addToSEETeamsRef: seeTeamsObj, showAddToSEETeams: showView });
                                                        });
                                                    });
                                            } else
                                                this.setState({ showAddToSEETeams: showView, isTeamLoading: false });
                                        });
                                    });
                            });
                        }
                    }
                }
            }
            else
                this.setState({ showAddToSEETeams: showView });

        } catch (error) {
            //  Logging.AppInsightsTrackException(AppSetting["appName"], "Exception occured at onAddToSEETeamClick() in " + this.fileName, error);
        }
    }

